.startup-projects-main {
    display: flex;
}

.startup-projects-main>* {
    flex: 1;
    margin-bottom: 30px;
}

.starup-project-image>img {
    max-width: 100%;
    height: auto;
}

.project-card-head {
    color: rgb(36, 41, 46);
    font-size: 25px;
    font-weight: 700;
    letter-spacing: -0.5px;
    margin: 0px;
}

.startup-project-text {
    display: flex;
    justify-content: space-between;
}

.project-card-description {
    color: rgb(88, 96, 105);
}

.startup-project-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: normal;
    margin: 0px;
}

.startup-project-subheading {
    margin: 0px;
    margin-bottom: 50px;
}

.subTitle {
    color: #868e96;
}

.saaya-health-div,
.nextu-div {
    cursor: pointer;
}

@media (max-width: 1380px) {
    .startup-project-heading {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    .starup-project-image {
        display: none;
    }

    .startup-project-heading {
        font-size: 30px;
        text-align: center;
    }

    .startup-projcet-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }
}