#topButton {
    visibility: hidden;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    border: none;
    outline: none;
    background-color: #55198b;
    color: white;
    cursor: pointer;
    padding: 15px;
    border-radius: 15px;
    font-size: 25px;
}

#topButton:hover {
    background-color: #000000;
    transition: all ease-in-out 0.2s;
}

@media only screen and (max-width: 768px) {
    #topButton {
      display: none;
    }
}