.square {
    width: 440px;
    height: 430px;
    background: white;
    border-radius: 4px;
    box-shadow: 0px 20px 50px #d9dbdf;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    margin-top: 30px;
}

.square:hover {
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
}

.blog-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: normal;
    margin: 0px;
}

.blog-subheading {
    margin: 0px;
    margin-bottom: 50px;
}

.blog-main-div>* {
    flex: 1;
    margin-bottom: 30px;
}

.blog-main-div {
    display: flex;
}

.blog-image-div>img {
    max-width: 100%;
    height: auto;
}

.blog-text-div {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: 32px;
}

.subTitle {
    color: #868e96;
}

@media (max-width: 1380px) {
    .blog-heading {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    .blog-heading {
        font-size: 30px;
        text-align: center;
    }

    .blog-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }
}

@media screen and (max-width: 480px) and (min-width: 300px) {
    .blog-text-div {
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
}