.talk-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: normal;
    margin: 0px;
}

.talk-subheading {
    margin: 0px;
}

.talk-cards-div {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    gap: 1rem 1rem;
}

.subTitle {
    color: #868e96;
}

@media (max-width: 1380px) {
    .talk-heading {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    .talk-heading {
        font-size: 30px;
        text-align: center;
    }

    .talk-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }
}