.App {
	text-align: center;
	width: 100%;
	overflow-x: hidden;
}

.App-logo {
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(0.625rem + 2vmin);
	color: white;
}

.App-link {
	color: #09d3ac;
}

.heading {
	color: black;
}

.subTitle {
	color: #868e96;
}
