.contact-div-main {
    display: flex;
}

.dark-mode a {
    color: white;
}

.contact-div-main>* {
    flex: 1;
}

.contact-subheading {
    margin: 0px;
}

.contact-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: 0px;
}

.contact-detail,
.contact-detail-email {
    margin-top: 20px;
    font-size: 40px;
    font-weight: 400;
    color: #868e96;
    text-decoration: none;
}

.contact-detail:hover,
.contact-detail-email:hover {
    color: black;
    text-shadow: 2px 1px 2px #b5b5b5;
    transition: all 0.3s;
}

.contact-text-div {
    margin-top: 1.5rem;
}

.contact-margin-top {
    margin-top: 4rem;
}

.contact-image-div>img {
    max-width: 100%;
    height: auto;
    margin-left: 1.5rem;
    margin-top: -4rem;
}

@media (max-width: 1380px),
(max-width: 768px) {
    .contact-heading {
        font-size: 56px;
        font-weight: 400;
        text-align: center;
    }

    .contact-heading {
        font-size: 40px;
        text-align: center;
    }

    .contact-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }

    .contact-text-div {
        text-align: center;
        margin-top: 1.5rem;
    }

    .contact-detail,
    .contact-detail-email {
        font-size: 20px;
    }

    .contact-image-div {
        display: none;
    }
}