.achievement-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: normal;
    margin: 0px;
}

.achievement-subheading {
    margin: 0px;
    margin-bottom: 50px;
}

.subTitle {
    color: #868e96;
}

@media (max-width: 1380px) {
    .achievement-heading {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    .achievement-heading {
        font-size: 30px;
        text-align: center;
    }

    .achievement-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }
}