.podcast-heading {
    font-size: 40px;
    font-weight: 400;
    line-height: 0px;
}

.podcast-subheading {
    margin: 0px;
    margin-bottom: 50px;
}

.podcast {
    width: 600px;
}

.podcast-main-div {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1rem 1rem;
}

.subTitle {
    color: #868e96;
}

@media (max-width: 1380px) {
    .podcast-heading {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    .podcast-heading {
        font-size: 30px;
        text-align: center;
    }

    .podcast-subheading {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }

    .podcast {
        width: 300px;
    }

    .podcast-main-div {
        text-align: center;
    }
}